import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom';
import MaterialList from './list'
import AddMaterial from './add'
import EditMaterial from './edit'

const MaterialRoute = props => {
  const { match } = props
	return (
		<Switch>
			<Redirect exact from={`${match.url}`} to={`${match.url}/list`} />
			<Route path={`${match.url}/add`} component={AddMaterial} />
			<Route path={`${match.url}/edit/:id/`} component={EditMaterial} />
			<Route path={`${match.url}/list/:tab?`} component={MaterialList}  />
		</Switch>
	)
}

export default MaterialRoute

